@import url(https://aaditya-baduni.github.io/vag-rounded-font.css);
@import url(https://fonts.googleapis.com/css2?family=Libre+Franklin&family=Merriweather&family=Roboto:wght@400;700&display=swap);

/* Libre Franklin font */

* {
	box-sizing: border-box;
}

body {
	background-color: rgb(86, 177, 146);
}


/* Website name */
#website-title {
	font-family: VagRounded, sans-serif;
	font-size: 1.8rem;
	color: yellow;
	font-weight: 700;
	letter-spacing: 1px;

	text-decoration: none;
	position: absolute;
}

#website-title:hover {
	color: rgb(220, 220, 0);
}

#logo-img {
	border-radius: 5px;
}

/* Page Title */
#title {
	text-align: center;
	color: white;
	font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
	font-weight: 400;
	margin-bottom: 1%;
}

#total-p {
	color: white;
	font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
	text-align: center;
	font-size: 1.3rem;
	margin-top: 0;
	text-decoration: underline;
}

/* The Games */
.game {
	width: 70%;
	height: 20%;
	border-radius: 10px;
	background-color: rgb(15, 0, 99);
	display: inline-block;

	margin: 2% 15% 2%;
	padding: 0.8%;
}

.images {
	border-radius: 5px;
	float: left;
	width: 70%;
	display: block;
	margin: 0 auto;
	text-align: center;

	margin-left: 15%;
	margin-right: 15%;
	margin-bottom: 20px;
}

h1 {
	text-align: center;
	font-family: 'Libre Franklin', sans-serif;
	font-weight: 100;
}

a {
	text-decoration: none;
	color: red;
}

a:hover {
	color: rgb(200, 0, 0);
}

.game-text {
	color: white;
	font-family: 'Libre Franklin', sans-serif;
	font-weight: 100;
	font-size: 1.5rem;
	text-align: center;
	margin-bottom: 20px;
}

/* Different screen size optimization */
@media (max-width: 860px) {
	#website-title {
		position: static;
		margin-bottom: 0;
	}
	#title {
		margin-top: 1%;
	}
	.game {
		width: 80%;
		margin-right: 10%;
		margin-left: 10%;
	}
	.game-text {
		font-size: 1.2rem;
	}
	.game-title {
		font-size: 1.5rem;
	}
}

@media (max-width: 760px) {
	.game {
		width: 90%;
		margin-left: 5%;
		margin-right: 5%;
	}
	#website-title {
		font-size: 1.5rem;
	}
}

@media (max-width: 564px) {
	#total-p {
		font-size: 1.1rem;
	}
}

/* Scrollbar */
::-webkit-scrollbar {
	width: 7px;
}

::-webkit-scrollbar-thumb {
	background: white;
	border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
	background: rgb(200, 200, 200);
}
